.Settings {
    margin: 2px;
}

.Settings .sections {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-flow: row wrap;
        flex-flow: row wrap;
}
.Settings .section {
    position: relative;
    -ms-flex-order: 1;
        order: 1;
    border: 1px solid #BBB;
    border-radius: 8px;
    margin: 10px 5px 10px 0;
    padding: 10px 8px;
}
.Settings .section.main {
    -ms-flex: 1 1 auto;
        flex: 1 1 auto;
}

.Settings .section span.warning{
    font-weight:bold;
    text-align: center;
    display:block;
}

.Settings .section span.error{
    color: red;
    float: right;
}
.Settings .section.side{
    -ms-flex: 0 0 auto;
        flex: 0 0 auto; width: 45%;
}
.Settings input, .Settings select {
    margin: 0px 0px 10px;
    width: 100%;
    display: block;
}

.main button {
    float: right;
    margin: 10px;
    padding: 10px;
}
.section textarea{
    resize: none;
    overflow: visible;
    border: 0 none;  outline : none;
    caret-color: transparent !important;
    display:block;
    width:100%;
    height: 75px;
    margin: 0px;
    white-space: normal;
    text-align: center;
    text-align-last: center;
    -webkit-box-shadow: none;
}

h3 {
    margin-top:0;
}


