.Payment {
    font-size: 16px;
}

.Payment label {
    display:block;
}
.displayErrors input:invalid {
    border-color: #cf4944;
}

.Payment span.hidden{
    display:none;
}

.Payment span.error{
    display:block;
    color: #cf4944;
    float: right;
    font-size: .75em;
}


.Payment label {
    margin-top: 0.5em;
    margin-bottom: 0;
}

.Payment {
    overflow:hidden;
    width:100%;
}

.Payment .hidden-input {
    display:none;
}

.Payment input  {
    padding: 8px;
    font-size:16px;
    width:100%;
    border: 1px solid #ccc;
    background-color: #fff;
}

.Payment input::-webkit-input-placeholder{
    color:#999;
}

.Payment input::-ms-input-placeholder{
    color:#999;
}

.Payment input::placeholder{
    color:#999;
}

.Payment iframe {
    border: none;
    width: 100%;
    height: 40px;
    margin:0 0 0 0;
}

.Payment label span {
    padding-top: 6px;
}

.Payment button {
    margin: 10px 0;
}

.name_wrapper_individual {
    display: -ms-flexbox;
    display: flex;
}

.customer_bank_account_holder_firstname {
    -ms-flex: 0 0 50%;
        flex: 0 0 50%;
    margin-right: 3px;
}

.customer_bank_account_holder_lastname {
    -ms-flex: 1 1;
        flex: 1 1;
    margin-left: 3px;
}



